.main {
    max-width: 1240px;
    display: flex;
    margin: 120px auto 0;
}

.link {
    text-decoration: none;
    color: #8585ad;
    cursor: pointer;
}

.activeLink {
    text-decoration: none;
    color: #f2f2f3;
}

.text {
    max-width: 320px;
}

.links {
    display: flex;
    flex-direction: column;
}

.form {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 480px;
    gap: 24px;
}

.buttons {
    display: flex;
    gap: 24px;
}