.list {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    gap: 16px;
    max-height: calc(100vh - 700px);
    overflow-y: scroll;
}

.name {
    display: flex;
    align-items: center;
    gap: 16px;
}

.item {
    display: flex;
    gap: 16px;
    align-items: center;
    justify-content: space-between;
}

.container {
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 620px;
}

.block {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.image {
    border: #801ab3 solid 2px;
    border-radius: 100px;
    width: 64px;
    height: 64px;
    object-fit: cover;
}

.text {
    width: 100%;
}

.span {
    display: flex;
    gap: 8px;
    align-items: center;
}

.number {
    text-align: center;
}