.item {
    display: flex;
    flex-direction: column;
    gap: 24px;
    border-radius: 40px;
    background-color: #1c1c21;
    cursor: pointer;
}

.container {
    display: flex;
    justify-content: space-between;
}

.total {
    display: flex;
    align-self: center;
}

.list {
    display: flex;
    list-style-type: none;
    margin: 0 16px 0;
    padding: 0;
}

.list_item {
    position: relative;
    border: #801ab3 solid 2px;
    border-radius: 100px;
    width: 64px;
    height: 64px;
    margin-left: -16px;
    z-index: 100;
}

.image {
    width: 64px;
    height: 64px;
    object-fit: cover;
    background-color: #1c1c21;
    border-radius: 100px;
}

.span {
    position: absolute;
    top: 20px;
    left: 0;
    width: 64px;
    height: 64px;
    color: #f2f2f3;
    text-align: center
}



